import React from 'react';
import { Link } from 'react-router-dom'

import './Header.scss';

const DeliveryHeader = () => (
  <header>
    <nav>
      <ul>
        <li><Link to='/home'>Home</Link></li>
        <li><Link to='/'>Login</Link></li>
        <li><Link to='/signup'>Signup</Link></li>
      </ul>
    </nav>
  </header>
)

export default DeliveryHeader;




